.doctorCard {
   display: flex;
    width:90vw !important;
    margin:auto;
    flex-wrap: wrap;
    justify-content: center ;
  
}

.card {
    margin: 1vw;
    width: 25vw !important;
}

@media screen and (max-width:900px) {
    .doctorCard {
        display: flex;
        width: 90vw;
        margin: auto;
        flex-wrap: wrap;
        justify-content: center;
    }

    .card {
        width: 40vw !important;
    }

}

@media screen and (max-width:600px) {
    .doctorCard {
        display: flex;
        width: 100vw;
        flex-wrap: wrap;
        justify-content: center;
    }

    .card {
        width: 40vw !important;
        
    }

}

@media screen and (max-width:459px) {
    .doctorCard {
        display: flex;
        flex-direction: column;
        width: 80vw !important;
        flex-wrap: wrap;
        margin: auto;
        justify-content: center;
    }

    .card {
        width: 80vw !important;

    }

}