.userCard{
    display: flex;
    width:90vw;
    margin: auto;
    flex-wrap:wrap  ;
    justify-content: center;


}
.card{
    margin:1vw;
    width:30vw;
}

@media screen and (max-width:900px) {
    .userCard {
            display: flex;
            
            margin: auto;
            flex-wrap: wrap
        }
    .card{
        width:30vw
    }
    
}

@media screen and (max-width:600px) {
    .userCard {
        display: flex;
        width: 100vw;
        flex-wrap:wrap

    }

    .card {
        width: 40vw;
        margin:5vw;
    }

}

@media screen and (max-width:459px) {
    .userCard {
        display: flex;
        flex-direction: column;
        width: 100vw;
        flex-wrap: wrap;
        margin:5vw;
    }

    .card {
        width: 80vw;
        
    }

}